import React from "react";
import { Breakpoint, DebugActionType } from "../../types";
import { useQuery } from "react-query3";
import { useParams } from "react-router-dom";
import { Button, PageHeader, Result, Space, Tag, Tooltip } from "antd";
import { ArrowDownOutlined, ArrowRightOutlined, ArrowUpOutlined, PlayCircleOutlined } from "@ant-design/icons";
import axios from "axios";
import DebugTerminal from "./debugTerminal";

export default function Debugger() {
    const { processId, runspaceId } = useParams();
    const [running, setRunning] = React.useState(false);
    const { isLoading, refetch } = useQuery<Breakpoint>(
        `/debugger/breakpoint/${processId}/${runspaceId}`, {
        onSuccess: () => setRunning(false),
        onError: () => setRunning(true)
    });

    const [disabled, setDisabled] = React.useState(false);

    const SetDebugAction = async (type: DebugActionType, command?: string) => {
        setDisabled(true);
        var response = await axios({
            method: "PUT",
            url: `/api/v1/debugger/breakpoint/${processId}/${runspaceId}`,
            data: {
                type,
                command
            },
        });

        await refetch();
        setDisabled(false);
        return response.data;
    }

    return (
        <PageHeader title="Debugger" subTitle={`Debugging process ID ${processId} and runspace ID ${runspaceId}`} tags={(running || isLoading) && <Tag color="green"><PlayCircleOutlined /> Running</Tag>}>

            {(!running || isLoading) ? (
                <Space direction="vertical" style={{ width: "100%" }}>
                    <Space direction="horizontal">
                        <Tooltip title="Continue">
                            <Button disabled={disabled || isLoading || running} icon={<PlayCircleOutlined />} onClick={() => SetDebugAction(DebugActionType.Continue)} />
                        </Tooltip>
                        <Tooltip title="Step Over">
                            <Button disabled={disabled || isLoading || running} icon={<ArrowRightOutlined />} onClick={() => SetDebugAction(DebugActionType.StepOver)} />
                        </Tooltip>
                        <Tooltip title="Step Into">
                            <Button disabled={disabled || isLoading || running} icon={<ArrowDownOutlined />} onClick={() => SetDebugAction(DebugActionType.StepInto)} />
                        </Tooltip>
                        <Tooltip title="Step Out">
                            <Button disabled={disabled || isLoading || running} icon={<ArrowUpOutlined />} onClick={() => SetDebugAction(DebugActionType.StepOut)} />
                        </Tooltip>
                    </Space>
                    <DebugTerminal processId={processId} runspaceId={runspaceId} refetch={refetch} />
                </Space>)
                :
                <Result title="Runspace is not stopped at a breakpoint" subTitle="Runspace is currently running a script." />}

        </PageHeader>
    );
}
