import React, { useEffect } from "react";
import { WebLinksAddon } from "xterm-addon-web-links";
import { FitAddon } from "xterm-addon-fit";
import { SearchAddon } from "xterm-addon-search";
import { XTerm } from "xterm-for-react";
import axios from "axios";
import { DebugActionType } from "../../types";

const fitAddon = new FitAddon();
const searchAddon = new SearchAddon();
const webLinksAddon = new WebLinksAddon();

const DebugTerminal = ({ processId, runspaceId, refetch }) => {
    const xtermRef = React.useRef<XTerm>(null);
    const xtermCommandRef = React.useRef<string>("");

    useEffect(() => {
        let termRefVar = xtermRef.current;
        fitAddon.fit();
        xtermRef.current.terminal.write("PowerShell Universal Debugger\r\n");

        const terminal = xtermRef.current.terminal;

        const onCommand = async (command) => {
            var result = await axios.put(`/api/v1/debugger/breakpoint/${processId}/${runspaceId}`, {
                type: DebugActionType.Command,
                command
            });

            refetch();

            return result.data.output;
        };

        const writePrompt = () => {
            onCommand("prompt").then(data => {
                if (!data) return;

                for (let i = 0; i < data.length; i++) {
                    terminal.write('\b \b');
                }

                terminal.write(data.trimEnd());
            });
        }

        xtermRef.current.terminal.onKey((key) => {
            if (key.key === "\r") {
                if (xtermCommandRef.current.toLocaleLowerCase() === "cls" || xtermCommandRef.current.toLocaleLowerCase() === "clear-host") {
                    terminal.clear();
                    writePrompt();
                    xtermCommandRef.current = "";
                    return;
                }

                onCommand(xtermCommandRef.current).then((data) => {
                    terminal.writeln(data);
                    writePrompt();
                    xtermCommandRef.current = "";
                });
            }

            // escape
            if (key.key === "\x1B") {
                if (xtermCommandRef.current.length > 0) {
                    for (let i = 0; i < xtermCommandRef.current.length; i++) {
                        terminal.write('\b \b');
                    }
                    xtermCommandRef.current = "";
                }
                return;
            }

            //backspace
            if (key.key === "\x7F") {
                if (xtermCommandRef.current.length > 0) {
                    xtermCommandRef.current = xtermCommandRef.current.substr(0, xtermCommandRef.current.length - 1);
                    terminal.write('\b \b');
                }

                return;
            }

            xtermRef.current.terminal.write(key.key);
            xtermCommandRef.current = xtermCommandRef.current + key.key;
        });

        writePrompt();

        return () => termRefVar.terminal.dispose();
    }, [xtermRef, processId, runspaceId, refetch]);

    return (
        <div className="height100">
            <XTerm
                ref={xtermRef}
                addons={[searchAddon, fitAddon, webLinksAddon]}
                options={{
                    //cols: 80,
                    cursorBlink: true,
                }}
                className="height100"
            />
        </div>

    );
};

export default DebugTerminal;
